





































































	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	export default{
		components: {uniPopup},
		data(){
			return{
				Signboard:'',
				Signguid:'',
				orderId:"", //接收下单页传过来的订单序号
				orderTips:false, //弹出框的显示隐藏
				orderMsg:"", //订单详细信息获取失败时的提示消息
				orderDetail:"", //订单明细
				orderData:"", //订单信息
				oldInfo:"", //历史记录
				historyOld:false
			}
		},
		methods:{
			/* 获取订单详情失败时弹出框的取消按钮 */
			orderPopup(evt){ 
				this.orderTips=false;
			},
			/* 获取订单详情失败时弹出框的确定按钮 --点确定的时候跳转回订单页面 */
			orderTipsYes(evt){
				uni.navigateTo({
					url:'../order/orderpageinfo'
				})
			},
			oldClick(evt){
				let osId=evt;
				console.log(osId);
				this.historyOld=true;
				uni.request({
					url:'http://192.168.199.194/mobile/order/orderParticular',
					method:'GET',
					data:{
						Ident_Signboard: this.Signboard,
						Ident_Signguid: this.Signguid,
						orderid:osId,
						check:1
					},
					success: (res) => {
						if(res.data.status == 0){
							this.orderData=res.data.orderData; //将获得的订单信息赋值存给this.orderData
							this.orderDetail=res.data.orderDetail; //将获得的订单明细赋值存给this.orderDetail
							uni.showToast({
								title:res.data.message,
								icon:'none'
							})
						}else{
							this.orderMsg=res.data.message;
							this.orderTips=true;
						}
					}
				})
			}
		},
		onLoad:function(options) {
			var jsonList=uni.getStorageSync("jsonList");
			var data = JSON.parse(jsonList); //JSON字符串转对象
			this.Signboard = data.Ident_Signboard;
			this.Signguid = data.Ident_Signguid;
			this.orderId=options.orderid;
			// console.log('传参过来的'+this.orderId);
			uni.request({
				url:'http://192.168.199.194/mobile/order/orderParticular',
				method:'GET',
				data:{
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid,
					orderid:this.orderId
				},
				success: (res) => {
					if(res.data.status == 0){
						console.log(res)
						this.orderData=res.data.orderData; //将获得的订单信息赋值存给this.orderData
						this.orderDetail=res.data.orderDetail; //将获得的订单明细赋值存给this.orderDetail
						this.oldInfo=res.data.oldInfo; //将获得的历史记录信息存进this.oldInfo
						console.log("oldInfo:");
						console.log(res.data.oldInfo);
						console.log(res.data.oldInfo.os_id);
						console.log("orderData:");
						console.log(res.data.orderData);
						console.log("orderDetail:");
						console.log(res.data.orderDetail);
						uni.showToast({
							title:res.data.message,
							icon:'none'
						})
					}else{
 						this.orderMsg=res.data.message;
 						this.orderTips=true;
					}
				}
			})
		},
		
	}
